
::-webkit-scrollbar {
    display: none;
}

.Burger {
    width: 100%;
    height: 100%;
    margin: auto;
    overflow: scroll;
    text-align: center;
}

@media (min-width: 500px) and (min-height: 400px){
    .Burger {
        width: 350px;
        height: 300px;
    }
}


@media (min-width: 500px) and (min-height: 401px){
    .Burger {
        width: 450px;
        height: 400px;
    }
}

@media (min-width: 1000px) and (min-height: 700px){
    .Burger {
        width: 700px;
        height: 600px;
    }
}